import React, { Component } from "react";
import {
  Navbar,
  Container,
  Brand,
  Form,
  FormControl,
  Button,
  Nav,
  NavDropdown,
} from "react-bootstrap";
import { Link } from "react-router-dom";

export default class Navigation extends Component {
  render() {
    const Gerb = "https://soliq.uz/assets/images/gerb.png",
      Flag = "https://soliq.uz/assets/images/flag2.png";
    return (
      <>
        <Navbar expand="lg">
          <Container>
            <Navbar.Brand>
              <a href="/" className="brandImg">
                <img src={Flag} alt="" />
                <img src={Gerb} alt="" />
              </a>
              <div className="brandName">
                <span className="name">
                  <h4>Buloqboshi tuman</h4>
                  <h4>Davlat Soliq Inspeksiyasi</h4>
                </span>
                <span className="phone">
                  <i class="fas fa-phone-alt"></i>
                  <h3 className="px-1">
                    <a
                      className="text-decoration-none text-dark"
                      href="tel:(74)773 25 69"
                    >
                      +99874 7732569
                    </a>
                  </h3>
                </span>
              </div>
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav
                className="me-auto my-4 my-lg-0"
                style={{ maxHeight: "100px" }}
              >
                <Link to="/about" className="nav-link">
                  INSPEKSIYA HAQIDA
                </Link>
                <Link to="/activity" className="nav-link">
                  FAOLIYAT
                </Link>
                <Link to="/documents" className="nav-link">
                  ME'YORIY HUJJATLAR
                </Link>
                <Link to="/services" className="nav-link">
                  XIZMATLAR
                </Link>
                <NavDropdown title="TIL" id="navbarScrollingDropdown">
                  <NavDropdown.Item href="#action5">
                    O'zbekcha
                    <img
                      src="https://cdn.countryflags.com/thumbs/uzbekistan/flag-round-250.png"
                      alt=""
                    />
                  </NavDropdown.Item>
                  <NavDropdown.Item href="#action6">
                    Русский
                    <img
                      src="https://cdn.countryflags.com/thumbs/russia/flag-round-250.png"
                      alt=""
                    />
                  </NavDropdown.Item>
                </NavDropdown>
              </Nav>
              <Form className="d-flex">
                <FormControl
                  type="search"
                  placeholder="Search"
                  className="me-2"
                  aria-label="Search"
                />
                <Button variant="outline-primary">Search</Button>
              </Form>
            </Navbar.Collapse>
            <div className="brn">
              <Link to="/" className="name text-decoration-none">
                <h6 className="px-2">Buloqboshi tuman</h6>
                <h6 className="px-2">Davlat Soliq Inspeksiyasi</h6>
              </Link>
            </div>
          </Container>
        </Navbar>
      </>
    );
  }
}
