import React, { Component } from "react";
import SimpleMap from "./OnlyMaps";

export default class Maps extends Component {
  render() {
    return (
      <div className="maps">
        <h2 className="mb-4">Aloqa</h2>
        <div className="row">
          <div className="col-md-12">
            <SimpleMap />
          </div>
          <div className="col-md-12 mt-5">
            <ul className="m-0 p-0">
              <li className="list-unstyled caption">
                <i class="fas fa-map-marker-alt"></i>
                <span>Adres:</span>
                <h5 className="px-3 pt-2">
                  Toshkent shahri, 100011, Abdulla Qodiriy ko'chasi, 13-a
                </h5>
              </li>
              <li className="list-unstyled caption">
                <i class="far fa-compass"></i>
                <span>Mo'ljal:</span>
                <h5 className="px-3 pt-2">Toshkent shahri, 100011</h5>
              </li>
              <li className="list-unstyled caption">
                <i class="far fa-clock"></i>
                <span>Ish vaqti:</span>
                <h5 className="px-3 pt-2">
                  9:00 dan 18:00 gacha, tushlik 13:00 dan 14:00 gacha
                </h5>
              </li>
              <li className="list-unstyled caption">
                <i class="far fa-calendar-alt"></i>
                <span>Dam olish kunlari:</span>
                <h5 className="px-3 pt-2">Shanba, Yakshanba</h5>
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}
