import React, { Component } from "react";
import Slider from "react-slick";

export default class SlickSlider extends Component {
  render() {
    var settings = {
      className: "",
      dots: false,
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      adaptiveHeight: true,
      autoplay: true,
      focusOnSelect: true,
      arrows: false,
    };
    return (
      <>
        <h4 className="headerTitle mb-4">
          <span className='press'>Yangiliklar</span>
          <i class="fas fa-grip-lines-vertical"></i>
          <span className='press pressedLink'>E'lonlar</span>
        </h4>

        <Slider {...settings} className="sliderItems">
          <div className="sliderItem">
            <img
              src="https://soliq.uz/storage/news/November2021/221c83ff29f24d21b8ccc77354559ba1_09112021.jpg"
              alt=""
            />
            <h6 className="cardTitle mt-2">
              <span className="titleDate">13/04/2021</span>
            </h6>
            <p className="cardText">
              <a href="#">
                “E-aktiv” – mahsulotlar hisobini yuritishning zamonaviy shakli
              </a>
            </p>
          </div>
          <div className="sliderItem">
            <img
              src="https://soliq.uz/storage/news/November2021/f0802d13206f49c19e81add97bfac60c_09112021.jpg"
              alt=""
            />
            <h6 className="cardTitle mt-2">
              <span className="titleDate">13/04/2021</span>
            </h6>
            <p className="cardText">
              <a href="#">
                “E-aktiv” – mahsulotlar hisobini yuritishning zamonaviy shakli
              </a>
            </p>
          </div>
          <div className="sliderItem">
            <img
              src="https://soliq.uz/storage/news/November2021/487315fa617a47b3bf87ae3043b01058_09112021.jpg"
              alt=""
            />
            <h6 className="cardTitle mt-2">
              <span className="titleDate">13/04/2021</span>
            </h6>
            <p className="cardText">
              <a href="#">
                “E-aktiv” – mahsulotlar hisobini yuritishning zamonaviy shakli
              </a>
            </p>
          </div>
        </Slider>
      </>
    );
  }
}
