import React, { useState } from "react";

export default function OnlyBoss(props) {

  console.log(props.data);


  const [moreInfo, setMoreInfo] = useState(false);

  return (
    <>
      <div className="card mb-5">
        <div className="row">
          <div className="col-md-3 bossImg">
            <img src={props.data.imgSrc} alt="" />
          </div>
          <div className="col-md-9 bossCaption">
            <div className="cardBody">
              <ul className="m-0 py-1">
                <li className="list-unstyled caption">
                  <i class="far fa-user"></i>
                  <span>{props.data.lavozim}</span>
                  <h4>{props.data.fish}</h4>
                </li>
                <li className="list-unstyled caption">
                  <i class="far fa-clock"></i>
                  <span>Fuqarolarni qabul qilish kunlari</span>
                  <p>Har kuni 09.00 dan 11.00 gacha</p>
                </li>
                <li className="list-unstyled caption">
                  <i class="fas fa-phone"></i>
                  <span>{props.data.tel}</span>
                  <p className="m-0 mt-2 bossAllInfo">
                    <span
                      className="pb-1"
                      onClick={() => {
                        setMoreInfo(!moreInfo);
                      }}
                    >
                      Mehnat faoliyati
                    </span>
                  </p>
                </li>
              </ul>
            </div>
          </div>
          {moreInfo ? (
            <div className="faoliyat col-md-12">
              <div>
                <p>{props.data.faoliyat}</p>
              </div>
            </div>
          ) : null}
        </div>
      </div>
    </>
  );
}
