import React, { Component } from "react";
import Slider from "react-slick";

export default class AllInfo extends Component {
  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }
  next() {
    this.slider.slickNext();
  }
  previous() {
    this.slider.slickPrev();
  }
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 1000,
      slidesToShow: 1,
      slidesToScroll: 1,
      adaptiveHeight: true,
    };
    return (
      <div className="sliderOffice">
        <h2 className='mb-4'>
          Buloqboshi tuman <br /> Davlat Soliq Inspeksiyasi
        </h2>
        <Slider ref={(c) => (this.slider = c)} {...settings}>
          <div key={1}>
            <img
              src="https://soliq.uz/storage/files/April2021/2c91808378c558ce0178cbe146b80004.jpg"
              alt=""
            />
          </div>
          <div key={2}>
            <img
              src="https://soliq.uz/storage/files/April2021/2c91808378c558ce0178cbdfc95b0001.jpg"
              alt=""
            />
          </div>
          <div key={3}>
            <img
              src="https://soliq.uz/storage/files/April2021/2c91808378c558ce0178cbe146b80004.jpg"
              alt=""
            />
          </div>
          <div key={4}>
            <img
              src="https://soliq.uz/storage/files/April2021/2c91808378c558ce0178cbdfc95b0001.jpg"
              alt=""
            />
          </div>
          <div key={5}>
            <img
              src="https://soliq.uz/storage/files/April2021/2c91808378c558ce0178cbe146b80004.jpg"
              alt=""
            />
          </div>
        </Slider>
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <button className="button btn" onClick={this.previous}>
            <i class="fas fa-chevron-left"></i>
          </button>
          <button className="button btn" onClick={this.next}>
            <i class="fas fa-chevron-right"></i>
          </button>
        </div>
        <div className="officeText">
          <h4 className="mt-4">Umumiy ma'lumot </h4>
          <p className="text">
            O'zbekiston Respublikasi Davlat soliq qo'mitasi (keyingi o'rinlarda
            qo'mita deb ataladi) soliq to'g’risidagi qonun hujjatlariga rioya
            etilishini va davlatning iqtisodiy manfaatlari himoya qilinishini
            ta'minlash sohasidagi nazorat bo'yicha respublika davlat boshqaruvi
            organi hisoblanadi.
          </p>
          <p className="text">
            Qo'mita O'zbekiston Respublikasi Vazirlar Mahkamasiga bo'ysunadi.
          </p>
          <p className="text">
            Qo'mita o'z faoliyatida O'zbekiston Respublikasi Konstitutsiyasiga
            va qonunlariga, O'zbekiston Respublikasi Oliy Majlisi palatalari
            qarorlariga, O'zbekiston Respublikasi Prezidentining farmonlari,
            qarorlari va farmoyishlariga, O'zbekiston Respublikasi Vazirlar
            Mahkamasining qarorlari va farmoyishlariga, shuningdek, ushbu
            Nizomga hamda boshqa qonun hujjatlariga amal qiladi.
          </p>
          <p className="text">
            Davlat soliq xizmati organlari tomonidan o'z vakolatlari doirasida
            qabul qilingan qarorlar davlat va xo'jalik boshqaruvi organlari,
            mahalliy davlat hokimiyati organlari, boshqa tashkilotlar va
            ularning mansabdor shaxslari, shuningdek, soliq to'lovchilar, shu
            jumladan yuridik va jismoniy shaxslarning ijro etishi uchun majburiy
            hisoblanadi.
          </p>
          <p className="text">
            O'zbekiston Respublikasi Davlat soliq qo'mitasi (keyingi o'rinlarda
            qo'mita deb ataladi) soliq to'g’risidagi qonun hujjatlariga rioya
            etilishini va davlatning iqtisodiy manfaatlari himoya qilinishini
            ta'minlash sohasidagi nazorat bo'yicha respublika davlat boshqaruvi
            organi hisoblanadi.
          </p>
        </div>
      </div>
    );
  }
}
